<template>
  <h1>Cuentas Socios</h1>
  <hr/>

  <div v-if="getVerFacturas">
      <VerCuentas />
  </div>
  <div v-else>
      <TableCuentas />
  </div>
</template>

<script>
import { defineAsyncComponent } from 'vue'
import { mapGetters } from 'vuex';

export default {
    components:{
    TableCuentas: defineAsyncComponent(() => import("@/modules/socios/components/Cuentas/TableCuentas")),
    VerCuentas: defineAsyncComponent(() => import("@/modules/socios/components/Cuentas/VerCuentas")),
},
    computed:{
        ...mapGetters('cuenta',['getIsLoading', 'getVerFacturas'])
    }
}
</script>

